import React, { useState } from 'react';

import './Contact.css';
import { TextField, Radio, FormControlLabel, FormLabel, FormControl, RadioGroup } from "@material-ui/core"




function Contact() {
    const [userRegistration, setuserregistration] = useState({
        firstname: "",
        lastname: "",
        email: "",
        phone: "",
        website: "",
        message: ""

    })

    const handleInput = (e) => {
        const name = e.target.name;
        const value = e.target.value;
        setuserregistration({ ...userRegistration, [name]: value });

    }
    const handleSubmit = (e) => {
        e.preventDefault();
        const newRecord = { ...userRegistration, id: new Date().getTime().toString() }
        console.log(newRecord);
        setuserregistration({
            firstname: "",
            lastname: "",
            email: "",
            phone: "",
            website: "",
            message: ""
        })
    }

    return (
        <div className="container m-auto bg-light bg-gradient mt-5">
            <div className="text-center p-5">
                <h1 className="hcontact fw-bold">Contact Us</h1>
                <h5 className="  text-muted"> Any questions or remarks? just write us a message! </h5>
            </div>
            <div className="row gx-0 m-auto p-3 main-contact rounded justify-content-center ">
                <div className="col-md-5 left-info">
                <div className="p-5">
                    <h4 className="">Contact Informtion</h4>
                    <p className="fillup fw-light">Fill up the form our team will get back to you within 24 hours.</p>
                  
                  <div className="mt-5 icondiv"> <i class="fas fa-phone-alt"></i> <span className="icons">+0984-567-8955</span></div>
                  <div className="mt-5 icondiv" ><i class="fas fa-envelope"></i><span className="icons">1hlo@flowbse.com</span></div>
                  <div className="mt-5 icondiv mb-0"><i class="fas fa-map-marker-alt"></i><span className="icons">102 Street 0452 Don</span></div>
                  </div><div><div className="circle"></div> <div className="quarter-circle" ></div> </div>
                  <div className="mt-5 text-center"><span className="footer-icons" ><i class="fab fa-facebook-f footer"></i></span>
                  <span  className="footer-icons"><i class="fab fa-twitter footer"></i>
                  </span> <span className="footer-icons"><i class="fab fa-instagram footer"></i></span>
                  <span className="footer-icons"><i class="fab fa-linkedin-in footer"></i></span></div>
                </div>



                <div className="col-md-7 ">
                    <form  >
                        <div className="mb-4">
                            <TextField label='First Name' placeholder='' value={userRegistration.firstname} className="textfield" name="firstname" onChange={handleInput} >
                            </TextField>
                            <TextField label='Last Name' className="textfield" placeholder='john doe' value={userRegistration.lastname} name="lastname" onChange={handleInput}>
                            </TextField>
                            <TextField label='Mail' placeholder='john@' type="email" className="textfield" value={userRegistration.email} name="email" onChange={handleInput}>
                            </TextField>
                            <TextField label='Phone' className="textfield" type="number" placeholder='99' name="phone" value={userRegistration.phone} onChange={handleInput}>
                            </TextField>
                        </div>
                        <div className="p-4">
                            <FormControl component="fieldset">

                                <FormLabel component="legend">What the of Website do you need? </FormLabel>
                                <RadioGroup row aria-label="gender" name="website" value={userRegistration.website} onChange={handleInput}>
                                    <FormControlLabel value="Web Design" control={<Radio />} className="r-color" label="Web Design " />
                                    <FormControlLabel value="Web Development" control={<Radio />} label="Web Development" className="r-color" />
                                    <FormControlLabel value="Logo Design" control={<Radio />} label="Logo Design" className="r-color" />
                                    <FormControlLabel value="other" control={<Radio />} label="Other" className="r-color" />

                                </RadioGroup>
                            </FormControl>
                        </div>
                        <div className="p-3"> <TextField label='Message' placeholder='Write Your Message' name="message" value={userRegistration.message} onChange={handleInput} fullWidth type="textarea" >
                        </TextField></div>
                        <div className=" text-end mt-5">
                            <button type="submit" onClick={handleSubmit} className="btn px-auto py-auto btn-lg me-md-2 send-m ">Send Message</button></div>
                    </form>
                </div>

            </div>
        </div>
    )
}

export default Contact;
